import React from "react";
import { useState,useEffect } from "react";

export const Input = ({ setBusquedas }) => {
  const [valor, setValor] = useState("");

  const handleInput = (e) => {
    setValor(e.target.value)
  };

   const handleForm = (e) =>{


    e.preventDefault();

   
      
    setBusquedas(valor.toLowerCase())
    setValor("")
    console.log("hecho")
    
   
   }

  return (
    <div>
      <form onSubmit={handleForm}>
        
        <input className="input" value={valor} type="text" onChange={handleInput} placeholder="pokemon รณ numero..."></input>
        
   
      </form>
    </div>
  );
};
