import React from "react";
import { GridGifs } from "./GridGifs";
import { Input } from "./Input";
import { useState } from "react";
import { BrowserRouter as Router, Switch, Route, Link } from "react-router-dom";

export const GiffApp = () => {
  const [busquedas, setBusquedas] = useState(25);

 const [activo, setactivo] = useState(true)

 const cambiarActivo = () =>{


  setactivo(false)
 

 }


  return (     <Router>
    <div className="container">
      <div className="pokedex">
        <img
          className="titulo"
          src={
            "https://images.wikidexcdn.net/mwuploads/esssbwiki/7/77/latest/20111028181540/TituloUniversoPok%C3%A9mon.png"
          }
        />

        { (activo !==false) &&
    <div className="flecha_pokeball">
        <img
          className="flecha"
          src="https://static.wixstatic.com/media/bf827f_db4157875df2460489709fcce4fbeb30~mv2.gif"
        ></img>

          <Link to="/pokemons">
          <img onClick={cambiarActivo}
            src="https://upload.wikimedia.org/wikipedia/commons/thumb/5/51/Pokebola-pokeball-png-0.png/601px-Pokebola-pokeball-png-0.png"
            className="pokeball"
          />
</Link>
      </div>

        }
          <Switch>
           

            <Route path="/pokemons">
              <Input setBusquedas={setBusquedas} />
              <GridGifs busquedas={busquedas} />
            </Route>
          </Switch>
        
      </div>
    </div></Router>
  );
};
