import React from "react";
import { useEffect, useState, useContext } from "react";
import { Boton } from "./Boton";
import { UserContext } from "./UserContext";

export const GridItem = (pokemonData) => {
  const {
    name = "",
    height = 0,
    abilities1 = null,
    abilities2 = null,
    numero,
    b_photo,
    fuerza,
    tipo,
  } = pokemonData;

  const llamar = async () => {
    const url = abilities1;
    const request = await fetch(url);
    const data = await request.json();
   

    const hability = {
      habilidad: data.flavor_text_entries[21].flavor_text,
    };

    sethabilidad(hability);
  };

  const llamarTipo = async () => {
    const url = tipo;
    const request = await fetch(url);
    const data = await request.json();

    const tipoPokemon = data.types[0].type.name;

   // settipo(tipoPokemon);
  };


  const [habilidade, sethabilidad] = useState("");

  const { habilidad } = habilidade;
const { setid } = useContext(UserContext);

  useEffect(() => {
    llamar();
    // llamarTipo();
  
  }, [pokemonData]);

 useEffect(() => {
  setid(numero)
 }, [pokemonData])


  

  return (
    <div>
      {name !== "" && (
        <div>
          <div className="container_pokemon">
            <h1>{name}</h1>
            <div className="container_foto">
              {" "}
              <img src={b_photo} className="poke_foto" />
              <a>#{numero}</a>
            </div>
            <p>
              <a className="blanco">altura </a>
              {height}0 cm
            </p>
            <p>
              <a className="blanco">poderes </a> {habilidad}
            </p>{" "}
            <p>
              <a className="blanco">fuerza </a>
              {fuerza}
            </p>
          </div>
        </div>
      )}
    </div>
  );
};
